/* src/components/Dashboard.css */
.dashboard-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #f8f9fa;
  }
  
  .dashboard-content {
    max-width: 600px;
    width: 100%;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    background-color: #fff;
    text-align: center;
  }
  
  .user-greeting {
    font-size: 24px;
    color: #007bff;
  }
  
  .user-email {
    color: #333;
  }
  
  .logout-link {
    display: inline-block;
    margin-top: 20px;
    padding: 10px;
    background-color: #dc3545;
    color: #fff;
    text-decoration: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .logout-link:hover {
    background-color: #c82333;
  }
  